import React, { FC } from 'react'
import clsx from 'clsx'
import { Typography } from '../../../Typography'
import { PaginatorItemProps } from '../../types'

const PaginatorItem: FC<PaginatorItemProps> = ({
  isActive = false,
  onClick = (): void => {},
  text = '',
  icon = null,
  className,
}) => {
  return (
    <div
      className={clsx(
        { 'Paginator__List-Number': !icon },
        { 'Paginator__List-Icon': icon },
        className,
      )}
      onClick={onClick}
    >
      {icon}
      {!icon && (
        <Typography
          className={clsx('Paginator__List-Item', {
            '--active': isActive,
            '--default': !isActive,
          })}
          text={text}
          type='Body1B'
        />
      )}
    </div>
  )
}

export default PaginatorItem
